  display: none;
}

/** Media Queries **/
html {
  box-sizing: border-box;
  font-size: 62.5%;
  /*1rem = 10px*/
}

*, *:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #FFF;
  font-size: 1.6rem;
  line-height: 1.5;
}

p {
  font-size: 2rem;
  font-family: Georgia, "Times New Roman", Times, serif;
  color: #000000;
  text-align: justify;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
}

.contenedor {
  max-width: 120rem;
  margin: 0 1.5rem;
}

@media (min-width: 920px) {
  .contenedor {
    margin: 0 auto;
  }
}
h1, h2, h3 {
  text-align: center;
  color: #000000;
  font-family: "Staatliches";
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.ir-arriba-btn {
  display: none; /* Oculto inicialmente */
  position: fixed; /* Posici√≥n fija en la ventana */
  z-index: 99; /* Asegura que est√© sobre otros elementos */
  border: none;
  border-radius: 1rem;
  background-color: #000000;
  color: #FFF;
  font-size: 2rem;
  bottom: 5rem;
  right: 2rem;
  padding: 1rem;
}
.ir-arriba-btn:hover {
  cursor: pointer;
  background-color: #FFF;
  color: #000000;
}

@keyframes rebote {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-4rem);
  }
  60% {
    transform: translateY(-2rem);
  }
}
/*Alert*/
.contenidoalert {
  font-size: 1.5rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(223, 49, 49);
}
.alerta.exito {
  background-color: #08a103;
}

.boton-verde {
  background-color: #08a103;
  color: #FFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #066f02;
  cursor: pointer;
}

.boton-rojo {
  background-color: rgb(223, 49, 49);
  color: #FFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-rojo:hover {
  background-color: #bf1e1e;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(223, 49, 49);
  color: #FFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #bf1e1e;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #08a103;
  color: #FFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-verde-block:hover {
  background-color: #066f02;
  cursor: pointer;
}

.header {
  background-color: #11949d;
}
.header .logo {
  margin: 1rem;
}
.header .logo__img {
  display: block;
  width: auto;
  margin: 0 auto;
}

.navegacion__enlace {
  display: block;
  text-align: center;
  color: #000000;
  font-size: 3rem;
  font-family: "Staatliches";
}
.navegacion__enlace--activo, .navegacion__enlace:hover {
  color: #FFF;
}

@media (min-width: 768px) {
  .header {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .navegacion {
    display: flex;
    column-gap: 2rem;
  }
  .navegacion__enlace {
    font-size: 3.5rem;
  }
}
.footer {
  background-color: #11949d;
  padding: 0.5rem 0;
  margin-top: 2rem;
}
.footer__texto {
  color: #000000;
  font-size: 2rem;
  font-family: "Staatliches";
  text-align: center;
  margin: auto;
}
.footer__social {
  text-align: center;
}
.footer__social a {
  color: #000000;
  font-size: 4rem;
}
.footer__social a:hover {
  color: #FFF;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2.5rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  padding: 1rem;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  width: 100%;
  display: block;
  background-color: #FFF;
  border: 0.1rem solid #e1e1e1;
  border-radius: 1rem;
  border-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.producto {
  background-color: #11949d;
  padding: 2rem;
  border-style: solid;
  border-width: 0.3rem;
  border-radius: 2rem;
  border-color: #000000;
}
.producto:hover {
  background-color: rgb(72, 179, 228);
  animation: rebote 1.5s infinite;
}
.producto__nombre {
  font-size: 2.5rem;
  color: #FFF;
}
.producto__precio {
  font-size: 3rem;
  color: #000000;
}
.producto__nombre, .producto__precio {
  font-family: "Staatliches";
  margin: 1rem 0;
  text-align: center;
  line-height: 1.2;
}
.producto__imagen {
  display: block;
  margin: 0 auto;
  border: 0.3rem solid #FFF;
  border-radius: 20rem;
}

.grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 920px) {
  .grid {
    grid-template-columns: repeat(5, 1fr);
  }
}

.grafico {
  min-height: 30rem;
  background-repeat: no-repeat;
  background-size: cover;
  grid-column: 1/3;
}

.producto_form p {
  text-align: left;
  margin: 1rem 0;
}
.producto_plataforma {
  font-family: "Staatliches";
  font-size: 2.5rem;
}
.producto_precio {
  font-size: 2rem;
}
.producto_cantidad {
  width: 20rem;
  margin-right: 5rem;
  border: 0.2rem solid #e1e1e1;
  padding: 0.8rem;
}
@media (max-width: 480px) {
  .producto .producto_form {
    margin: 0 auto;
  }
}

.nota {
  font-size: 1.5rem;
  margin-top: 3rem;
}

.nosotros__contenido {
  text-align: justify;
}
.nosotros__imagen {
  border: solid 0.3rem #000000;
  border-radius: 2rem;
}

@media (min-width: 768px) {
  .nosotros__imagen {
    width: 25rem;
  }
  .nosotros {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 5rem;
  }
}
@media (min-width: 920px) {
  .nosotros__contenido {
    width: 50rem;
  }
}
.bloque {
  text-align: justify;
}
.bloque__titulo {
  margin: 0;
}
.bloque__imagen {
  display: block;
  width: auto;
  margin: 0 auto;
  filter: brightness(0%);
}

@media (min-width: 768px) {
  .bloques {
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
table.tabla {
  border-spacing: 0;
  text-align: center;
  font-size: 1.5rem;
}
table.tabla thead {
  background-color: #11949d;
}
table.tabla thead th {
  color: #FFF;
}
table.tabla img {
  width: 10rem;
  border-radius: 2rem;
}

.imagen-smoll {
  width: 20rem;
}

@media (min-width: 920px) {
  table.tabla {
    width: 100%;
    font-size: 2rem;
  }
  table.tabla thead th {
    padding: 2rem;
  }
  table.tabla .imagen-tabla {
    width: 10rem;
  }
}
@media (max-width: 480px) {
  table.tabla {
    width: 100%;
    font-size: 1.5rem;
    display: block;
  }
  table.tabla tbody,
  table.tabla tr {
    display: block;
    margin-bottom: 1rem;
  }
  table.tabla td {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
  }
  table.tabla th {
    display: block;
  }
  table.tabla thead {
    display: block;
    width: 20rem;
    margin: 0 auto;
  }
  table.tabla .accion {
    display: block;
    margin: 0 auto;
    width: 20rem;
  }
  table.tabla .imagen-tabla {
    width: 15rem;
  }
}